$color-bg: #EBECF0;
$color-shadow: #BABECC;
$color-white: #FFF;


.footer{
    position: relative;
    z-index: 25;
    background-color: $color-bg;
    .footer__wrapper{
        display: flex;
        justify-content: space-between;
        @media (max-width:1270px){
          flex-direction: column;
          align-items: center;
        }
        .footer__container{
            position: relative;
            background-color:$color-bg;
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            font-family: 'Bai Jamjuree';
            .footer__img_bg1{
              position: absolute;
              right: 40%;
              top: 180px;
              width: 220px;
            }
            .footer__img_bg2{
              position: absolute;
              left: -130px;
              top: 250px;
              width: 250px;
            }
            .footer__img_bg3{
              position: absolute;
              right: 300px;
              top: 500px;
              width: 300px;
              @media (max-width:1270px){
                top: 400px;
                right: 200px;
              }
              @media (max-width:650px){
                top: 400px;
                right: 0px;
                width: 200px;
              }
            }
            .footer__contact__images{
              position: absolute;
              left: 10%;
              bottom: 0;
              z-index: 1;
              width: 100%;
              circle {
                fill: transparent;
                stroke: #0011cf;
                stroke-width: 2;
                animation: rotate 10s linear infinite alternate;;
                }
                
                @keyframes rotate {
                0% { transform: scale(1.2); }
                100% { transform: scale(1); }
                }
            }
            @media (max-width:1270px){
              width: 100%;
            }
        .footer__logo{
          width: 700px;
          font-size: 10vw;
          color: #0011cf;
          font-family: "BeatriceBold";
          text-transform: uppercase;
          h5{
            color: $color-bg;
          }
          @media (max-width:1270px){
            width: 100%;
          }
        }
        .footer__contact{
          width: 400px;
          margin: 0 0 0 auto;
          box-shadow: 0px 8px 10px #84868e;
          padding: 50px 55px;
          border-radius: 30px;
          z-index: 50;
          backdrop-filter: blur(10px);
          animation: footer__contact 3s ease infinite alternate;
          @keyframes footer__contact {
            0%{rotate: -4deg;transform: translate(16px, 15px);}
            100%{rotate: 4deg; transform: translate(-16px, -15px);}
          }
          &:hover {
            animation-play-state: paused;
          }
          :not(:last-child){
            margin-bottom: 3px;
          }
          @media (max-width:1270px){
            margin: 0 auto;
          }
          @media (max-width:550px){
            animation: none;
            margin-bottom: 20px;
          }
          @media (max-width:410px){
            width: 100%;
          }
          h4{
            text-align: center;
            font-size: 60px;
            font-weight: 700;
            color: #0011cf;
            font-family: "BeatriceBold";
            @media (max-width:410px){
              font-size: 40px;
            }
          }
          .footer__contact__cosial{
            display: flex;
            align-items: center;
            gap: 10px;
            justify-content: center;
            img{
              width: 40px;
              height: 40px;
              
            }            
          }
          form{
            width: 100%;
            h6{
              font-size: 18px;
              color: #0011cf;
            }
            input{
              width: 100%;
              border-radius: 30px;
              border: 1px solid #0011cf;
              font-size: 18px;
              padding: 5px 20px;
              color: #0011cf;
            }
            textarea{
              resize: none;
              outline: none;
              border: 1px solid #0011cf;
              border-radius: 30px;
              width: 100%;
              padding: 20px;
              font-style: 25px;
            }
            button{
              background-color: #0011cf;
              font-size: 30px;
              padding: 5px 0;
              width: 100%;
              font-weight: 700;
              color: #FFF;
              border-radius: 30px;
              font-family: "BeatriceBold";
              transition: .3s ease-in-out;
              &:hover{
                background-color: #0b18ac;
                letter-spacing: 2px;
              }
            }
          }
        }





